@media (max-width: 767px) {
  .header {
    position: sticky;
    height: 32px; }
  .burger {
    display: block;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    width: 26px;
    height: 12px;
    span {
      display: block;
      width: 100%;
      height: 2px;
      margin-bottom: 2px;
      background-color: #fff;
      transition: all .5s; }
    &_active {
      span {
        margin-bottom: -2px;
        &:nth-child(1) {
          transform: translateY(5px) rotate(-45deg); }
        &:nth-child(2) {
          display: none; }
        &:nth-child(3) {
          transform: translateY(5px) rotate(45deg); } } } }


  .menu__list {
    position: fixed;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    top: 0;
    left: -100%;
    width: 50%;
    height: 100vh;
    background-color: rgba(0, 0, 0, .9);
    transition: all .7s;
    &_active {
      left: 0; }
    li + li {
      border: none; }
    &-item {
      padding: 20px 3px; }
    &-item:first-child {
      padding: 20px 5px; }
    &-link {
      font-size: 20px;
      padding: 20px 0px; } }
  .about {
    min-height: 430px; }
  .about__header {
    height: 62px;
    position: relative;
    &-content {
      display: flex;
      justify-content: center; }
    &-logo {
      margin-top: 0; }
    &-logo-img {
      width: 130px;
      position: absolute;
      left: 5%;
      top: 38%; }
    &-logo-descr {
      display: none; }
    &-contact-text {
      display: none; }
    &-contact-phone {
      font-size: 12px;
      position: absolute;
      top: 20%;
      right: 15%; }
    &-call {
      margin: 0;
      width: 110px;
      height: 22px;
      font-size: 10px;
      position: absolute;
      top: 50%;
      right: 12%; } }
  .about__content {
    &-title {
      font-size: 30px;
      line-height: 35px;
      &-text {
        margin-top: 5px;
        font-size: 20px;
        line-height: 25px; } }
    &-text {
      display: none; }
    &-btn {
      margin-top: 70px;
      font-size: 16px;
      line-height: 50px;
      width: 210px;
      height: 50px; } }
  .benefits {
    &__title {
      font-size: 16px;
      line-height: 16px; }
    &__content {
      padding-right: 16px;
      padding-left: 16px;
      &-item-text-title {
        margin-bottom: 0;
        font-size: 16px;
        line-height: 21px; }
      &-item-text-text {
        font-size: 16px;
        line-height: 21px; } } }
  .title {
    margin-top: 16px;
    font-size: {} }
  .title-descr {
    font-size: 14px;
    line-height: 21px;
    margin-bottom: 27px; }
  .mobile {
    padding: 25px 0 40px;
    &__title {
      font-size: 16px;
      line-height: 16px; }
    &__content-item {
      width: 290px;
      height: 254px; }
    &__content-item-text {
      font-size: 18px; } }
  .possibilities {
    background-image: none;
    min-height: 200px;
    padding: 25px 0 30px;
    &__title {
      display: none;
      &-descr {
        display: none; } }
    &__car-img {
      max-width: 300px;
      margin-top: 20px; }
    &__descr {
      color: #fff;
      font-size: 17px;
      line-height: 24px;
      max-width: 270px; } }
  .require {
    padding: 20px 0 30px;
    &__item {
      margin: 30px 0 0px;
      &-list {
        padding-left: 30px;
        font-size: 16px;
        line-height: 24px;
        li {
          margin-bottom: 0; } }
      &-descr {
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 0; } } }
  .citys {
    min-height: 300px;
    padding: 24px 0 20px;
    &__title {
      font-size: 16px;
      line-height: 16px;
      margin-bottom: 11px;
      &-descr {
        font-size: 14px;
        line-height: 21px; } }
    &__content-btn {
      margin-top: 180px; } }
  .footer {
    position: relative;
    max-height: none;
    // padding: 0
    &__social {
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-65%); }
    &__menu:before {
      width: 255px; }
    &__menu {
      &-main li {
        padding: 10px 10px; }
      &-main a {
        font-size: 13px;
        line-height: 14px; }
      &-other {
        display: flex;
        flex-wrap: wrap; }
      &-other li {
        padding: 10px 20px; }
      &-other a {
        font-size: 11px;
        line-height: 11   px; } }
    &__app {
      &-link {
        position: absolute;
        bottom: -30px;
        left: 50%;
        transform: translateX(-50%); } } } }

